export const init_test_tabs_data = (data=[])=>{
    return (dispatch)=>{
        try{
            //
            const ResData = [...initState,...data].reduce( (prev,item)=>{
                return [...prev,{...item}];
            },[] )
            dispatch({ type:types.INIT_TEST_TABS_DATA,data: ResData })
            return message.success("");
        }catch(err){
            return message.error(err.message);
        }
    }
}

export const set_test_tabs_data = (data=[])=>{
    return (dispatch)=>{
        try{
            //1 data
            //2.redux
            dispatch({ type:types.SET_TEST_TABS_DATA,data })
            return message.success("");
        }catch(err){
            return message.error( err.message )
        }
    }
}