import {Component} from "react"
import {connect} from "react-redux"
import {withRouter} from "react-router-dom"
import "./CommonCollapseItemsExcel.scss"
import {
    Row,
    Col,
    Input,
    Button,
    message,
} from "antd"
//import {saveAs} from "file-saver"
import ExportJsonExcel from "js-export-excel"

class CommonCollapseItemsExcel extends Component{

    state = {
        filename: "",
    }


    //
    downloadSelectFileToExcel = () => {
        try{
            let dataTable = [];  //excel
            const option = {};  //optionexcel
            const {filename} = this.state;
            const {
                dataSource=[
                    {id:1,title:"123"},
                    {id:2,title:"1234"},
                    {id:3,title:"12345"},
                    {id:4,title:"123456"},
                    {id:5,title:"1234567"},
                    {id:6,title:"12345678"},
                ],
                popColumnsObj={},
                selectedRowKeys=[],
                toSelectedRowKeys = (param={selectedRowKeys:[]})=>{
                    this.props.toSelectedRowKeys(param);
                }
            } = this.props;
            const localColumnsObj = {
                id : {
                    title: 'ID',
                    dataIndex: 'key',
                    fixed : "left",
                    width:20,
                    sorter: (a, b) => parseInt(a.key) - parseInt(b.key),
                },
                ...popColumnsObj,
            }
            dataTable  = selectedRowKeys.reduce( (prev,item) => {
                return dataSource.find( row => parseInt(row.id) === parseInt(item) ) ? [...prev,dataSource.find( row => parseInt(row.id) === parseInt(item) )]:prev;
            } ,[])
            if( dataTable.length === 0 ){
                throw new Error("");
            }
            option.fileName = filename || window.dateFormat( (new Date()).getTime(),"Y-m-d_H-i-s");  //excel
            const sheetFilter = Object.keys( localColumnsObj );
            const sheetHeader = sheetFilter.reduce( (prev,item)=>{
                return [...prev,localColumnsObj[item].title];
            },[] )
            option.datas = [
                {
                    sheetData: dataTable,  //excel
                    sheetName: window.dateFormat( (new Date()).getTime(),"Y-m-d H:i:s"),  //excelsheet
                    sheetFilter: sheetFilter,  //
                    sheetHeader: sheetHeader,  //excel
                }
            ]
            let toExcel = new ExportJsonExcel(option);  //excel
            toExcel.saveExcel();  //excel
            toSelectedRowKeys();
        }catch(err){
            return message.error( err.message );
        }
    }

    //
    downloadFileToExcel = () => {
        try{
            let dataTable = [];  //excel
            const option = {};  //optionexcel
            const {filename} = this.state;
            const {
                dataSource=[
                    {id:1,title:"123"},
                    {id:2,title:"1234"},
                    {id:3,title:"12345"},
                    {id:4,title:"123456"},
                    {id:5,title:"1234567"},
                    {id:6,title:"12345678"},
                ],
                popColumnsObj={},
                //selectedRowKeys=[],
                toSelectedRowKeys = (param={selectedRowKeys:[]})=>{
                    this.props.toSelectedRowKeys(param);
                }
            } = this.props;
            const localColumnsObj = {
                id : {
                    title: 'ID',
                    dataIndex: 'key',
                    fixed : "left",
                    width:20,
                    sorter: (a, b) => parseInt(a.key) - parseInt(b.key),
                },
                ...popColumnsObj,
            }
            dataTable  = dataSource;
            if( dataTable.length === 0 ){
                throw new Error("");
            }
            option.fileName = filename || window.dateFormat( (new Date()).getTime(),"Y-m-d_H-i-s");  //excel
            const sheetFilter = Object.keys( localColumnsObj );
            const sheetHeader = sheetFilter.reduce( (prev,item)=>{
                return [...prev,localColumnsObj[item].title];
            },[] )
            option.datas = [
                {
                    sheetData: dataTable,  //excel
                    sheetName: window.dateFormat( (new Date()).getTime(),"Y-m-d H:i:s"),  //excelsheet
                    sheetFilter: sheetFilter,  //
                    sheetHeader: sheetHeader,  //excel
                }
            ]
            toSelectedRowKeys();
            let toExcel = new ExportJsonExcel(option);  //excel
            toExcel.saveExcel();  //excel
        }catch(err){
            return message.error( err.message );
        }
    }

    render(){
        const {
            selectedRowKeys
        } = this.props;
        const {filename} = this.state;
        //console.log(selectedRowKeys);
        return (
            <>
                <div className="CommonCollapseItemsExcelComponent">
                    <Row className="content">
                        <Col span={6} className="content_col_one">
                            
                            <span style={{
                                fontSize:"16px",
                                fontWeight:"800",
                                color:"red",
                                paddingLeft:"5px",
                                paddingRight:"5px",
                            }}>{selectedRowKeys.length}</span>
                            
                        </Col>
                        <Col span={6} className="content_col_two">
                            <Input addonBefore="" className="content_col_two_text" addonAfter=".xlsx" value={filename} onChange={
                                (e)=>{
                                    const filename = e.target.value;
                                    this.setState({filename},()=>{
                                        console.log( this.state.filename )
                                    });
                                }
                            } placeholder="()" />
                        </Col>
                        <Col span={6} className="content_col_three">
                            <Button type="dashed" shape={"round"} className="content_col_three_btn" onClick={()=>{
                                this.downloadSelectFileToExcel()
                            }}></Button>
                        </Col>
                        <Col span={6} className="content_col_four">
                            <Button type="primary" shape={"round"} className="content_col_four_btn" onClick={()=>{
                                this.downloadFileToExcel()
                            }}></Button>
                        </Col>
                    </Row>
                </div>
            </>
        );
    }

}

export default connect(
    store=>({
        SystemConfigData : store.SystemConfigData,
    }),{
        
    }
)( withRouter(CommonCollapseItemsExcel) );