//tree
window.listToTree = (MenuList=[])=>{
    //
    let treeList = MenuList.reduce((prev,cur)=>{
        prev[cur['id']] = cur;
        return prev
    },{})
    let result = MenuList.reduce((prev,cur)=>{
        let pid = cur.pid;
        // pid0cur
        // parentcur
        let parent = treeList[pid]
        // console.log(parent,1)
        // curchildren
        if(parent){
            // parentcur
            parent.children? parent.children.push(cur) : parent.children = [cur]
        } else{
            // cur
            prev.push(cur)
        }
        return prev
    },[]);
    return result;
}


//
window.dateFormat = function (timestamp, formats ='Y-m-d H:i:s'){
    // formats
    // 1. Y-m-d
    // 2. Y-m-d H:i:s
    // 3. Ymd
    // 4. Ymd Hi
    formats = formats || 'Y-m-d';

    var zero = function (value) {
        if (value < 10) {
            return '0' + value;
        }
        return value;
    };
    var myDate = timestamp? new Date(timestamp): new Date();
    var year = myDate.getFullYear();
    var month = zero(myDate.getMonth() + 1);
    var day = zero(myDate.getDate());
    var hour = zero(myDate.getHours());
    var minite = zero(myDate.getMinutes());
    var second = zero(myDate.getSeconds());
    return formats.replace(/Y|m|d|H|i|s/ig, function (matches) {
        return ({
            Y: year,
            m: month,
            d: day,
            H: hour,
            i: minite,
            s: second
        })[matches];
    });
}

//
window.number_format = (number, decimals, dec_point = ".", thousands_sep=",") => {
    /*
    * 
    * number
    * decimals
    * dec_point
    * thousands_sep
    * */
    number = (number + '').replace(/[^0-9+-Ee.]/g, '');
    var n = !isFinite(+number) ? 0 : +number,
        prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
        sep = (typeof thousands_sep === 'undefined') ? ',' : thousands_sep,
        dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
        s = '',
        toFixedFix = function (n, prec) {
            var k = Math.pow(10, prec);
            return '' + Math.ceil(n * k) / k;
        };
 
    s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.');
    var re = /(-?\d+)(\d{3})/;
    while (re.test(s[0])) {
        s[0] = s[0].replace(re, "$1" + sep + "$2");
    }
 
    if ((s[1] || '').length < prec) {
        s[1] = s[1] || '';
        s[1] += new Array(prec - s[1].length + 1).join('0');
    }
    return s.join(dec);
}

//
window.arr_slice = (arr,len = 10)=>{
    let result = [];
    for( let i = 0;i<arr.length;i+=len ){
        result.push(arr.slice(i,i+len));
    }
    return result;
}

/**/
//n
window.throttle = (fn, ms = 1000)=>{
	let timerId // id
	return function () {
		// 
		if (!timerId) {
			timerId = setTimeout(() => {
				// id
				timerId = null
				fn.apply(this, arguments)
			}, ms)
		}
	}
}

/**/
//nn
window.debounce = (fn, ms=1000)=>{
    let timeoutId
    return function () {
      clearTimeout(timeoutId)
      timeoutId = setTimeout(() => {
        fn.apply(this, arguments)
      }, ms)
    }
}

