import {Component} from "react"
import {connect} from "react-redux"
import {withRouter} from "react-router-dom"
import {
    Modal,
    message,
} from "antd"
import * as iconMap from '@ant-design/icons';
const {confirm} = Modal;
export const WithComponentCommon = (CustomComponent)=>{
    class WarpperComponent extends Component{

        render(){
            return (
                <>
                    <CustomComponent {...Object.assign(
                        {},
                        {
                            ...this.props,
                            iconMap,
                        },
                        {
                            toConfirm:(Obj)=>{
                                const {
                                    title = "",
                                    icon = "ExclamationCircleOutlined",
                                    content = "",
                                    onOk = ()=>{
                                        console.log(111)
                                    },
                                } = Obj;
                                const CurIcon = iconMap[icon];
                                confirm({
                                    title: title,
                                    icon: <CurIcon />,
                                    content: content,
                                    onOk,
                                    onCancel:()=>{
                                      message.success("");
                                    },
                                });
                            }
                        }
                    )}></CustomComponent>
                </>
            );
        }

    }
    return connect(
        store => ({
            SystemConfigData : store.SystemConfigData,
        }),{

        }
    )( withRouter(WarpperComponent) )
} 