import Mock from "mockjs"
import {
    checkAuth,
} from "./common"
const List = [];
List.push(Mock.mock({
    id:1,
    pid:0,
    account : "@cname",
    "invit|6": "@character",
    age:"@integer(18,23)",
    region:"@region",
    province:"@province",
    county:"@county",
    header:"@img",
    create_time : "@datetime",
    current_time:"@now",
    path:"",
    status : "@boolean",
}))
const count = 300;
for (let i = 1; i <= count; i++) {
    List.push(
      Mock.mock({
        id: i+1,
        pid:`@integer(1, ${i})`,
        account: "@cname",
        "invit|6": "@character",
        age:"@integer(18,23)",
        region:"@region",
        province:"@province",
        county:"@county",
        header:"@img",
        create_time:"@datetime",
        current_time:"@now",
        path:"",
        status:"@boolean",
      })
    );
}
//
const ListObj = List.reduce( (prev,item)=>{
    return {...prev, [item.id]:item }
} ,{})
List.forEach( item => {
    let pathArr = [];
    //
    const pathFun = (item)=>{
        pathArr = [item.id,...pathArr];
        const parent = ListObj[ item['pid'] ] || false;
        if(parent){
            //
            pathFun(parent);
        }
        //
        return `-${pathArr.join("-")}-`;
    }
    item.path = pathFun(item);
} ) 
const newList = List;

//
export const getList = (...param)=>{
    const data = newList;
    if( !checkAuth(param[1].headers) ){
        return {
            code : 300,
            message : "fail",
            data : [],
        }
    }
    return {
        code : 200,
        message : "success",
        data,
    }
}

export const addUser = (...param)=>{
    try{
        if( !checkAuth(param[1].headers) ){
            return {
                code : 300,
                message : "fail",
                data : [],
            }
        }
        const resData = newList;
        const maxId = resData.reduce( (prev,item)=>{
            return prev > parseInt(item.id) ? prev : parseInt(item.id);
        },0)
        const currentId = maxId+1;
        const {
            pid,
            account,
            invit,
            age,
            region,
            province,
            county,
            header,
            create_time,
            current_time,
            status,
        } = JSON.parse(param[1].body);
        const parent = resData.find( item=>parseInt(item.id) === pid ) || false;
        if( !parent ){
            throw new Error("");
        }
        const currentData = {
            id:currentId,
            pid,
            account,
            invit,
            age,
            region,
            province,
            county,
            header,
            create_time,
            current_time,
            path: `${parent['path']}${currentId}-`,
            status,
        }
        const autoData = Mock.mock({
            id:1,
            pid:1,
            account : "@cname",
            "invit|6": "@character",
            age:"@integer(18,23)",
            region:"@region",
            province:"@province",
            county:"@county",
            header:"@img",
            create_time : "@datetime",
            current_time:"@now",
            path:"",
            status : "@boolean",
        });
        const addData = Object.keys(currentData).reduce( (prev,item)=>{
            return {...prev,[item]:currentData[item] ? currentData[item] : autoData[item]}
        },{} )
        resData.push(addData);
        //
        return {
            code: 200,
            message : "",
            data:[],
        } 
    }catch(err){
        return {
            code: 400,
            message: err.message,
            data : [],
        }
    }
}

//
export const getPageData = (...param)=>{
    if( !checkAuth(param[1].headers) ){
        return {
            code : 300,
            message : "fail",
            data : [],
        }
    }
    const {
        page=1,           //
        pageSize=10,       //
        searchAge="",      //
        searchRegion="",   //
        searchProvince="", //
        searchCounty="",   //
    } = JSON.parse(param[1].body);
    const resData = newList;
    const start = (page - 1) * pageSize;
    const end = page * pageSize;
    const mockList = resData.filter((item) => {
        if( searchAge && parseInt(item.age) !== parseInt(searchAge) ) return false;
        if( searchRegion && item.region !== searchRegion ) return false;
        if( searchProvince && item.province !== searchProvince ) return false;
        if( searchCounty && item.county !== searchCounty ) return false;
        return true;
    });
    const pageList = mockList.slice(start, end);
    return {
        code: 200,
        message: "success",
        data : {
            listCount: mockList.length,
            list : pageList,
        }
    }
}

//id
export const getOne = (...param)=>{
    if( !checkAuth(param[1].headers) ){
        return {
            code : 300,
            message : "fail",
            data : [],
        }
    }
    const {id} = JSON.parse(param[1].body);
    const data = newList.find( item => item.id === id );
    return {
        code: 200,
        message : "success",
        data,
    } 
}

export const deleteOne = (...param)=>{
    try{
        if( !checkAuth(param[1].headers) ){
            return {
                code : 300,
                message : "fail",
                data : [],
            }
        }
        const {id} = JSON.parse(param[1].body);
        const resData = newList;
        //
        const index = resData.findIndex( (item)=> item.id === id );
        if( index === -1 ){
            throw new Error("");
        }
        //
        const son = resData.find( item => parseInt(item.pid) === parseInt(id) ) || false;
        if( son ){
            throw new Error("");
        }
        //
        resData.splice(index,1);
        //
        return {
            code: 200,
            message : "",
            data:[],
        } 
    }catch(err){
        return {
            code: 400,
            message : err.message,
            data:[],
        } 
    }
}

export const editData = (...param)=>{
    try{
        if( !checkAuth(param[1].headers) ){
            return {
                code : 300,
                message : "fail",
                data : [],
            }
        }
        const {
            id,
            pid,
            account,
            invit,
            age,
            region,
            province,
            county,
            header,
            create_time,
            current_time,
            status,
        } = JSON.parse(param[1].body);
        const resData = newList;
        const localCurrentData = resData.find( (item)=> parseInt(item.id) === parseInt(id) )||false;
        if(!localCurrentData){
            throw new Error("");
        }
        //
        if( parseInt(localCurrentData["pid"]) !== parseInt(pid) ){
            //
            //
            const localParentData = resData.find( (item)=>parseInt(item.id) === parseInt(pid) ) || false;
            if( !localParentData ){
                throw new Error("");
            }
            //
            const reg = new RegExp(`${localCurrentData["path"]}`);
            //console.log(reg);
            if( reg.test( localParentData["path"] ) ){
                throw new Error("");
            }
            //console.log(localCurrentData["path"]);
        }
        //
        const localCurrentIndex = resData.findIndex( (item)=>parseInt(item.id) === parseInt(id) );
        const updateData = {
            id,
            pid,
            account,
            invit,
            age,
            region,
            province,
            county,
            header,
            create_time,
            current_time,
            status,
        }
        resData.splice(localCurrentIndex,1,updateData);
        //
        if( parseInt(localCurrentData["pid"]) !== parseInt(id) ){
            //
            const OldListObj = resData.reduce( (prev,item)=>{
                return {...prev, [item.id]:item }
            } ,{})
            //
            resData.forEach( item => {
                let pathArr = [];
                //
                const pathFun = (item)=>{
                    pathArr = [item.id,...pathArr];
                    const parent = OldListObj[ item['pid'] ] || false;
                    if(parent){
                        //
                        pathFun(parent);
                    }
                    //
                    return `-${pathArr.join("-")}-`;
                }
                item.path = pathFun(item);
            } )
        }
        //
        return {
            code: 200,
            message : "",
            data:[],
        } 
    }catch(err){
        return {
            code: 400,
            message: err.message,
            data : [],
        }
    }
}