import {Component} from "react"
import {connect} from "react-redux"
import {withRouter} from "react-router-dom"
import {
    WithContentTempContainer,
    WithUserListPageContainer,
} from "../../../container"
import {
    Row,
    Col,
    Button,
    message,
} from "antd"
import CommonTextArea from "../../../components/Common/TextArea"
import {
    AddUserListDataAction,
} from "../../../redux/actions"


class Add extends Component{

    state = {
        record:{
            account:"",
            age:"",
            county:"", 
            create_time:"2021-10-21 20:00:00",
            current_time:"2021-10-21 20:00:00",
            header:"",
            invit:"",
            pid:1,
            province:"",
            region:"",
            status:false,
        },
        editData : {
            account : {
                type:"text",
                name:"account",
                title:"",
                value:"",
                placeholder:"",
                options:[],
            },
            age : {
                type:"number",
                name:"age",
                title:"",
                value:"",
                placeholder:"",
                options:[],
            },
            county : {
                type:"text",
                name:"county",
                title:"",
                value:"",
                placeholder:"",
                options:[],
            },
            create_time : {
                type:"time",
                name:"create_time",
                title:"",
                value:"",
                placeholder:"",
                options:[],
            },
            current_time : {
                type:"time",
                name:"current_time",
                title:"",
                value:"",
                placeholder:"",
                options:[],
            },
            header : {
                type:"icon",
                name:"header",
                title:"",
                value:"",
                placeholder:"",
                options:[],
            },
            invit : {
                type:"text",
                name:"invit",
                title:"",
                value:"",
                placeholder:"",
                options:[],
            },
            pid : {
                type:"select",
                name:"pid",
                title:"ID",
                value:"",
                placeholder:"ID",
                options:[
                    
                ],
            },
            province:{
                type:"text",
                name:"province",
                title:"",
                value:"",
                placeholder:"", 
                options:[],
            },
            region:{
                type:"text",
                name:"region",
                title:"",
                value:"",
                placeholder:"", 
                options:[],
            },
            status:{
                type:"switch",
                name:"status",
                title:"",
                value:false,
                placeholder:"", 
                options:[],   
            }
        }
    }

    componentDidMount(){
        this.initEditData();
    }

    initEditData = ()=>{
        const {UserTreeOptionsData} = this.props;
        const {editData,record} = this.state;
        const newEditData = Object.keys(editData).reduce( (prev,item) => {
            if(item === "pid"){
                return {...prev,[item]:{...editData[item],value:record[item],options:UserTreeOptionsData}}
            }else{
                return {...prev,[item]:{...editData[item],value:record[item]}}
            }
            
        },{});
        this.setState({editData:newEditData});
    }

    render(){
        const {editData} = this.state;
        const editDataArr = Object.values(editData);
        return (
            <>
                <Row>
                    {
                        editDataArr.map( item=>{
                            return (
                                <Col span={24} key={item.name}>
                                    <CommonTextArea {...Object.assign(
                                        {},
                                        {
                                            ...item,
                                        },
                                        {
                                            set_form_data : (Obj)=>{
                                                const currentEditData = {...editData,...Obj};
                                                this.setState({editData:currentEditData})
                                            },
                                        }
                                    )}></CommonTextArea>
                                </Col>
                            )
                        } )
                    }
                </Row>
                <Row>
                    <Col span={12} style={{
                        height:"80px",
                    }}>
                        <Button type="dashed" shape="round" style={{
                            width:"50%",
                            position:"absolute",
                            top:"50%",
                            left:"50%",
                            transform:"translate(-50%,-50%)"
                        }} onClick={()=>{
                            this.initEditData();
                        }}></Button>
                    </Col>
                    <Col span={12} style={{
                        height:"80px",
                    }}>
                        <Button type="primary" shape="round" style={{
                            width:"50%",
                            position:"absolute",
                            top:"50%",
                            left:"50%",
                            transform:"translate(-50%,-50%)"
                        }} onClick={ 
                            async()=>{
                                const {editData} = this.state;
                                const reqParam = Object.values(editData).reduce( (prev,item)=>{
                                    return {...prev,[item.name]:item.value}
                                } ,{})
                                const res = await this.props.AddUserListDataAction(reqParam);
                                res && message.success(res); 
                                this.initEditData(); 
                            }
                        }></Button>
                    </Col>
                </Row>
            </>
        );
    }

}

export default connect(
    store => ({
        SystemConfigData : store.SystemConfigData,
        UserTreeOptionsData : store.UserTreeOptionsData,
    }),{
        AddUserListDataAction,
    }
)( withRouter( WithContentTempContainer( WithUserListPageContainer(Add) ) )  )